export const getStatus = (status, amount) => {
  switch (status) {
    case 'VIEW':
      return 'просмотр'
    case 'NEW':
      return 'создан'
    case 'BOOKING':
      return 'забронирован'
    case 'CANCEL':
    case 'CANCEL_CONFIRM':
    case 'CANCEL_NEW':
    case 'CANCEL_BOOKING':
      return 'аннулирован'
    case 'CONFIRM':
      return 'оплачен'
    case 'RETURN':
      return `возвращен - ${amount} грн`
    case 'NEW_ERROR':
      return 'ошибка создания'
    case 'BOOKING_ERROR':
      return 'ошибка бронирования'
    case 'CANCEL_ERROR':
      return 'ошибка аннулирования'
    case 'CONFIRM_ERROR':
      return 'ошибка оплаты'
    case 'RETURN_ERROR':
      return 'ошибка возврата'
    case 'REMOVE':
      return 'удалена позиция заказа'
    case 'UNAVAILABLE':
      return 'недоступен'
    case 'DISCOUNT':
      return 'скидка'

    default:
      return ''
  }
}
