import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { useHistory } from 'react-router'
import styled from 'styled-components'
import Order from '../components/Orders/Order'
import { ReactComponent as ArrowBackIcon } from '../images/arrowBack.svg'
import { ordersAll } from '../redux/orders/ordersActions'
import { getOrders } from '../redux/orders/ordersSelectors'

const OrdersResultPage = () => {
  const dispatch = useDispatch()
  const history = useHistory()
  const orders = useSelector(getOrders).orders

  const goBack = () => {
    dispatch(ordersAll(null))
    history.push('/orders')
  }
  console.log(orders)
  return (
    <>
      <Header>
        <ArrowBackIcon onClick={goBack} />
        <Title>Результат</Title>
      </Header>
      <List>
        {orders.length > 0 && orders.map(order => <Order key={order.id} order={order} />)}
      </List>
    </>
  )
}

export default OrdersResultPage

const Header = styled.header`
  display: flex;
  align-items: center;
  background-color: ${prop => prop.theme.mainColor};
  padding: 12.4px 24px;
`
const Title = styled.p`
  margin-left: 20px;
  font-size: 24px;
`
const List = styled.ul`
  max-width: 500px;
  padding: 10px;
`
