import React, { useState } from 'react'
import styled from 'styled-components'
import { getStatus } from '../../services/getStatus'
import { ReactComponent as PhoneIcon } from '../../images/phone.svg'
import { ReactComponent as MoreIcon } from '../../images/moreVert.svg'
import { Popover } from '@material-ui/core'

const Order = ({ order }) => {
  const [anchorEl, setAnchorEl] = useState(null)

  const openPopover = event => {
    setAnchorEl(event.currentTarget)
  }
  const closePopover = () => {
    setAnchorEl(null)
  }
  const open = Boolean(anchorEl)

  return (
    <Box margin-bottom="8px">
      <div style={{ marginBottom: '6px' }}>
        <Flex>
          <Text bold="700">Заказ {order.id}</Text>
          <Text bold="700"> {order.price.amount} грн</Text>
        </Flex>
        <Text bold="400">
          {order.from}-{order.to}
        </Text>
        <Text>{order.departure}</Text>
        <Text14>{order.station}</Text14>
      </div>
      {order.services.map(service => (
        <Customer key={service.id}>
          <FlexStart>
            <Phone href={`tel: +${service.customer.phone}`}>
              <PhoneIcon />
            </Phone>
            <div>
              <Text bold="700">
                Билет {service.id} (
                {getStatus(
                  service.operations[service.operations.length - 1].status,
                  service.operations[service.operations.length - 1].price.amount
                ).toUpperCase()}
                )
              </Text>
              <Text bold="700">{service.customer.name}</Text>
              <Text>{service.customer.phone}</Text>
              {service.operations.map(el => (
                <Text14>
                  {getStatus(el.status).toUpperCase()} - {el.date.slice(0, 19)}
                </Text14>
              ))}
            </div>
          </FlexStart>
          <IconBox onClick={openPopover}>
            <MoreIcon />
          </IconBox>
        </Customer>
      ))}
      <Popover
        open={open}
        anchorEl={anchorEl}
        onClose={closePopover}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
      >
        <StyledButton>Автоматический возврат</StyledButton>
        <StyledButton>Индивидуальный возврат</StyledButton>
      </Popover>
    </Box>
  )
}

export default Order

const Box = styled.li`
  ${props => props}
  border-bottom: 1px solid;
  padding: 10px 0 6px 0;
  &:last-child {
    margin: 0;
    border-bottom: none;
  }
`
const Flex = styled.div`
  display: flex;
  justify-content: space-between;
`
const Text = styled.p`
  font-size: 18px;
  font-weight: ${props => props.bold};
  line-height: 1.3;
`
const Text14 = styled.p`
  font-size: 14px;
`

const Phone = styled.a`
  margin-right: 10px;
`
const Customer = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
`
const FlexStart = styled.div`
  display: flex;
  align-items: center;
`
const IconBox = styled.div`
  padding: 6px;
  cursor: pointer;
`
const StyledButton = styled.button`
  display: block;
  width: 100%;
  padding: 20px;
  margin: 0;
  border: none;
  border-bottom: 1px solid;
  background: none;
  &:last-child {
    border-bottom: none;
  }
`
